import Vue from "vue";
import VueRouter from "vue-router";
import store from "../store/index";



import Home       from "@/views/Home.vue";
import About      from "@/views/About.vue";
import Login      from "@/views/Login.vue";
import Register   from "@/views/Register.vue";
import Logout     from "@/views/Logout.vue";
import Profile    from "@/views/Profile.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },


  {
    path: "/About",
    name: "About",
    component: About
  },

  {
    path: "/Login",
    name: "Login",
    component: Login, 
    meta: {
      requiredGuest: true
    }
  },

  {
    path: "/Register",
    name: "Register",
    component: Register,
    meta: {
      requiredGuest: true
    }
  },

  {
    path: "/Profile",
    name: "Profile",
    component: Profile,
    meta: {
      requiredAuth: true
    }
  },

  {
    path: "/Logout",
    name: "Logout",
    component: Logout
  },

];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next ) => {
  if(to.matched.some(record => record.meta.requiredAuth)) {
    if(!store.getters.isLogedIn){
      //Not Authorized user trying to login protected page, redirect to the login page 
      next('/login');
    } else {
      next();
    }
  }else if(to.matched.some(record => record.meta.requiredGuest)){
    if(store.getters.isLogedIn){
      next('/profile')
    } else {
      next()
    }
  } else {
    next();
  }
})
export default router;
