<template>
  <v-container>
    <v-row>
        <v-col cols="12">
            <v-row justify="center" align="center">
                <v-card width="600">
                <v-card-title>
                    <h1 class="display-1">Register</h1>
                    </v-card-title>
                    <v-card-text>
                    <v-form>
                        <v-text-field
                          label ="Name"
                          v-model="name"
                        >
                        </v-text-field>
                        <v-text-field
                          label="Email"
                          v-model="email"
                        >
                        </v-text-field>
                        <v-text-field
                            label ="magacaaga (Username)"
                            prepend-icon ="mdi-account-circle"
                            v-model="username"
                            >
                        </v-text-field>
                        <v-text-field
                            v-model="password"
                            prepend-icon ="mdi-lock"
                            :append-icon ='showPassword ? "mdi-eye" :  "mdi-eye-off"'
                            @click:append ="showPassword=!showPassword"
                            :type ='showPassword ? "text": "password"'
                            label ="Password">
                        </v-text-field>
                        <v-text-field
                            v-model="confirm_password"
                            prepend-icon ="mdi-lock"
                            :append-icon ='showPassword ? "mdi-eye" :  "mdi-eye-off"'
                            @click:append ="showPassword=!showPassword"
                            :type ='showPassword ? "text": "password"'
                            label ="Confirmd Password">
                        </v-text-field>

                    </v-form>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                    
                    <v-spacer></v-spacer>
                    <v-btn color ="success"
                        @click="registerUser"
                    >Register</v-btn>
                    </v-card-actions>
                </v-card> 
                
            </v-row>
        </v-col>
    </v-row>
    <v-row justify="center">
        <span>Hadii adan diwaan ku jirto halkan riix </span>
    </v-row>
    </v-container>
</template>

<script>
import {mapActions} from "vuex";
export default {

  data() {
    return {
      name:     "",
      email:    "",
      username: "",
      password: "",
      confirm_password: "",
      showPassword: true


    }
  }, 
  methods: {
    ...mapActions(['register']),
    registerUser() {
      let user = {
        name: this.name,
        email: this.email,
        username: this.username,
        password: this.password,
        confirmed_password: this.confirm_password
      }
      this.register(user).then(res => {
        if(res.data.success){
          this.$router.push('/login')
        }
      })
    }
  }

}
</script>

