<template>
    <v-container>
    <v-row>
        <v-col cols="12">
            <v-row justify="center" align="center">
                <v-card width="600">
                <v-card-title>
                    <h1 class="display-1">Loging</h1>
                    </v-card-title>
                    <v-card-text>
                    <v-form>
                        <v-text-field
                            label ="magacaaga (Username)"
                            prepend-icon ="mdi-account-circle"
                            v-model="username"
                            >
                        </v-text-field>
                        <v-text-field
                            v-model="password"
                            prepend-icon ="mdi-lock"
                            :append-icon ='showPassword ? "mdi-eye" :  "mdi-eye-off"'
                            @click:append ="showPassword=!showPassword"
                            :type ='showPassword ? "text": "password"'
                            label ="Password">
                        </v-text-field>
                    </v-form>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                    
                    <v-spacer></v-spacer>
                    <v-btn color ="info"
                        @click="loginUser"
                    >Loging</v-btn>
                    </v-card-actions>
                </v-card> 
                
            </v-row>
        </v-col>
    </v-row>
    <v-row justify="center">
        <span>Hadii adan diwaan gashaneyn halkan riix si aad isu diwaan geliso</span>
    </v-row>
    </v-container>
</template>

<script>

    import {mapActions} from "vuex";

export default {

  name: 'App',
  components: {
    
  },
  data: function() {
      return {
        username: "",
        password: "",
        showPassword: true
      }
  },

  methods: {
      ...mapActions(['login']),
      loginUser() {
          let user = {
              username: this.username,
              password: this.password
          }
          this.login(user).then(res => {
              if(res.data.success){
                    this.$router.push('/profile');       
              }
          }).catch(err => {
              console.log(err);
          })
      }

  }
};
</script>
