<template>
  <v-container>

    <span>User name: {{user.username}}</span>
    <br>
    <span>
      Full Name : {{user.name}}
    </span>
    <br>
    <span>Email Address: {{user.email}}</span>
  </v-container>
</template>

<script>
import {mapActions, mapGetters} from "vuex"
export default {
    
    methods: {
      ...mapActions(['getProfile'])
    },
    created() {
      this.getProfile();
    },
    computed:mapGetters(['user']),
}
</script>

