
<template>
    <v-tabs
       
        background-color="primary"
        dark
    >
        <v-tab
            :to="{name: 'Home'}"
        >
            Home
        </v-tab>
        <v-tab
            :to="{name: 'About'}"
        >
            About
        </v-tab>
        <v-tab
            :to="{name: 'Login'}"
            v-if="!isLogedIn"
        >
            Login
        </v-tab>
        <v-tab
            :to="{name: 'Register'}"
            v-if="!isLogedIn"
        >
            Register
        </v-tab>
        <v-tab
            :to="{name: 'Profile'}"
            v-if="isLogedIn"
        >
            Profile
        </v-tab>
        <v-tab
            :to="{name: 'Logout'}"
            v-if="isLogedIn"
            @click="logoutUser"
        >
            Logout
        </v-tab>
    </v-tabs>
</template>

<script>
import {mapGetters, mapActions}  from "vuex";

export default {
    
    computed: {
        ...mapGetters(['isLogedIn']),
        
    },
    methods: {
        ...mapActions(['logout']),
        logoutUser() {
            this.logout();
              
        }
        

    }

}
</script>

